@import url('https://fonts.googleapis.com/css2?family=Roboto&display=swap');

:root {
  --background-body: #292929;
  --background-content: #1d1d1d;
  --text-color: #e5e5e5;
}

body {
  margin: 0;
  font-size: 1rem;
  font-family: 'Roboto', sans-serif;
  background: var(--background-body);
  color: var(--text-color);
}


svg {
  width: 1.5rem;
}

button.icon {
  background: none;
  outline: none;
  border: none;
  cursor: pointer;
}

.container {
  max-width: 60rem;
  margin: 0 auto;
  padding: 0 0.5rem 0 0.5rem;
  display: flex;
  align-items: center;
  height: 100%;
}

header.header {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 4.5rem;
  background: var(--background-content);
  box-shadow: 0 1px 1px rgba(0, 0, 0, 0.3);
  z-index: 10;
}

header .logo-wrapper {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
}

header .logo {
  display: flex;
  align-items: center;
  cursor: default;
}

header .logo span {
  margin-left: 0.5rem;
}


header .logo span.title {
  font-size: 1.5rem;
  color: #2297EC;
  font-weight: bold;
}

.article {
  margin: 4rem 0;
}

.post-wrapper {
  width: 100%;
}

.code {
  position: relative;
}

.code .copy-icon {
  position: absolute;
  top: 1rem;
  right: 1rem;
  z-index: 5;
}

.container .post-wrapper img {
  margin-bottom: 4rem;
}

.CopiedIcon {
  background-color:#2297EC; color:#FFFFFF; padding: 5px
}

.introduction {
    text-align:justify;
    /* white-space: pre;
    word-break: normal;
    word-spacing: normal;
    overflow-wrap: normal; */
    color: rgb(238, 238, 238);
    background: rgb(47, 47, 47);
    font-family: "Roboto Mono", monospace;
    font-size: 1em;
    line-height: 1.5em;
    /* tab-size: 4; */
    padding: 15px;
    margin-bottom: 5rem;
    /* hyphens: none; */
}

.description {
    text-align:justify;
    color: rgb(238, 238, 238);
    background: rgb(47, 47, 47);
    font-family: "Roboto Mono", monospace;
    font-size: 1em;
    line-height: 1.5em;
    padding: 15px;
}



.main-sub-title {
  color: #CC0000;
  /* color: #2297EC; */
  font-size: 1.1rem;
  font-family: "Roboto Mono", monospace;
  font-weight: bold;
}
.sub-title {
  color: #2297EC;
  font-size: 1.1rem;
  font-family: "Roboto Mono", monospace;
  font-weight: bold;
}

.byme {
  color: #2297EC;
  font-size: .9rem;
}

.profilephoto img {
  width: 50px;
  border-radius: 50%;
  }